:root {
	--LoginScreen: rgb(255, 255, 255);
	--sesionScreen: rgb(255, 255, 255);
	--registroScreen: rgb(255, 255, 255);
	--fotoAvatar:5rem;
	--prueba: rgb(253, 15, 15);
	--prueba2: rgb(57, 230, 22);

}

.Login {
	background-color: var(--LoginScreen);
    margin-bottom: 1%;
    margin-top: 15vh;
    border-radius: 1rem;
    
}

.Registro {
	background-color: var(--registroScreen);
    align-content: center;
	text-align: center;
	
}

.iniciarSesion {
	width: 100%;
	height: 100%;
	background-color: var(--sesionScreen);
	align-content: center;
	text-align: center;
	
}
.avatar {
	height: 8rem;
	width: 8rem;
	background-image: url("../../assets/images/logo2.png");
    background-repeat: no-repeat;
    background-size: 97%;
}
.avatarDash {
	height: 4rem;
	width:  4rem;
	background-image: url("../../assets/images/logo2.png");
    background-repeat: no-repeat;
    background-size: 97%;
}
.btnPersonalizado{ 
    margin-bottom:2%;
    margin-top:2%
}
.espaciador{ 
    margin-bottom:2%;
    margin-top:2%
}
.gatito{
	height: 10rem;
	width: 10rem;
	background-image: url("https://plantillasdememes.com/img/plantillas/yo-habia-ponido-mis-cosas-aqui01561772726.png");
    background-repeat: no-repeat;
    background-size: 97%;
}
.avatarPerfil{
	height:var(--fotoAvatar);
	width:var(--fotoAvatar);
	background-image: url("../../assets/images/sergio.jpg");
	border:solid black 2px;	
	border-radius: 3rem;
    background-repeat: no-repeat;
    background-size: 100%;
}
.avatarPerfil2{
	height:var(--fotoAvatar);
	width:var(--fotoAvatar);
	background-image: url("../../assets/images/gatito.jpg");
	border:solid black 2px;	
	border-radius: 3rem;
    background-repeat: no-repeat;
	background-size: 100%;
	margin-left: 3rem;
	
   	
}
.top{
	margin-top: 5%;
}
 .left{
	 margin-left: 10%;
 }

 /*Botones*/


 .espaciador-botones{
	margin-bottom:2%;
    margin-top:2%;
    margin-left:2%;
    margin-right:2%;
 }

 .centrador{
	 text-align: center;
 }
 .saludo{
	 font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
	 color: rgb(37, 179, 9);
 }
 .p1{
	 background-color: var(--prueba)

 }
 .p2{
	 background-color: var(--prueba2)
 }
 .p3{	
	 background-color: var(--prueba2)
 }